:root {
    --primary-bg: #ffffff;
    --primary-text: #000000;
    --accent-color: #007bff;
  }
  
  [data-theme='dark'] {
    --primary-bg: #121212;
    --primary-text: #ffffff;
    --accent-color: #1e90ff;
  }
  
  body {
    margin: 0;
    font-family: 'Segoe UI', sans-serif;
    background-color: var(--primary-bg);
    color: var(--primary-text);
    scroll-behavior: smooth;
    transition: background-color 0.3s, color 0.3s;
  }
  
  nav a {
    color: var(--primary-text);
    text-decoration: none;
  }
  
  nav a:hover {
    color: var(--accent-color);
  }
  
  section {
    padding: 4rem 1rem;
  }
  
  h1, h2, h3 {
    color: var(--primary-text);
  }
  